import React, { Component } from "react";
// import PropTypes from "prop-types";

import {instance , urls } from "../axios/DefaultAxios";

class Header extends Component {

  handle_exit = (event) => {
    instance.post(urls.logout,{token:localStorage.getItem("auth_token")});
    localStorage.removeItem("auth_token");
    this.props.history.push("/login");
  }
  render() {
    return (
      <header id="header" className="row justify-content-around border">
        <div className="col-md-4 col-sm-3 col-xs-3" id="joboonja_logo">
          <img
            id="icon"
            src="/static/assets/logo/logo.png"
            alt="joboonjaLogo"
            onClick={() => {
              this.props.history.push("/");
            }}
          />
          
        </div>
        <div className="col-md-4 col-sm-5 col-xs-6">
          <div id="header_title">
          آزماشگاه هوش مصنوعی
          </div>
        </div>
        <div className="col-md-1 col-sm-0 col-xs-0"></div>
        <div id="exit_buttom" className="col-md-2 col-sm-3 col-xs-3">
          
          <a  onClick={this.handle_exit}>
          <span>
          خروج
          </span>
          </a>
        </div>
        <div className="col-md-1 col-sm-0 col-xs-0"></div>
      </header>
    );
  }
}

