import React, { Component } from "react";
import { toast, ToastContainer } from "react-toastify";
import {instance , urls} from "../axios/DefaultAxios";
// import PropTypes from "prop-types";

import "react-toastify/dist/ReactToastify.css";

import "./AuthPage.css";

class LoginPage extends Component {
  componentDidMount() {
    document.title = "ورود";
  }

  state = {
    first_name: "",
    first_name_valid: true,
    last_name: "",
    last_name_valid: true,
    phone : "",
    phone_valid: true,
  };

  formValidationCheck = event => {
    event.preventDefault();
    var valid = true;
    if (!this.state.first_name.match(/^[a-zA-Z-]+$/iu)) {
      this.setState({first_name_valid:false,first_name:""});
      valid = false;
    }

    if (!this.state.last_name.match(/^[a-zA-Z-]+$/iu)) {
      this.setState({last_name_valid:false,last_name:""});
      valid = false;
    }

    if (!this.state.phone.match(/^[0-9]+$/)) {
      this.setState({phone_valid:false,phone:""});
      valid = false;
    }
    if(!valid)
      return;
    
    instance.post(urls.login,{first_name:this.state.first_name,
                              last_name:this.state.last_name,
                              phone_number:this.state.phone})
    .then( response => {
      localStorage.setItem("auth_token",response.data.token);
      this.props.history.push("/");
    })
    .catch( error => {
      if(error.response.data){
        console.log(error.response.data);
        for (var err in error.response.data){
          toast.error(error.response.data[err]);
        }
      }
      else{
        console.log(error);
        toast.error("خطای سرور");
      }
      this.setState({first_name:"",last_name:"",phone:""});
    })
  };


  field_has_value(val){
    if(val){
      return " has-val ";
    }
    else{
      return "";
    }
  }


  field_is_valid(val){
    if(!val){
      return " alert-validate ";
    }
    else{
      return "";
    }
  }


  render() {

    return (
      <div className="limiter main " >
        <ToastContainer autoClose={5000} position={toast.POSITION.TOP_RIGHT} />
        <div className="container-login100 container-contact2">
          <div className="wrap-login100">
            <div className="login100-form-title">
              <span className="login100-form-title-1">ورود</span>
            </div>

            <form className="contact2-form validate-form" onSubmit={this.formValidationCheck}>
					

					<div className={"wrap-input2 validate-input"+this.field_is_valid(this.state.first_name_valid)}>
						<input className={"input2 "+this.field_has_value(this.state.first_name)} type="text" name="first_name"
            value={this.state.first_name}
            onChange={event => {
              this.setState({ first_name: event.target.value });
            }}
            onFocus={event => {
              this.setState({ first_name_valid: true})
            }}/>   
            {/* add has-val class to input if it has value and is not null */}
						<span className="focus-input2" data-placeholder="نام"></span>
					</div>

					<div className={"wrap-input2 validate-input"+this.field_is_valid(this.state.last_name_valid)}>
						<input className={"input2 " + this.field_has_value(this.state.last_name)} type="text" name="last_name"
            value={this.state.last_name}
            onChange={event => {
              this.setState({ last_name: event.target.value });
            }}
            onFocus={event => {
              this.setState({ last_name_valid: true})
            }}/>
            
						<span className="focus-input2" data-placeholder="نام خانوادگی"></span>
					</div>

          <div className={"wrap-input2 validate-input"+this.field_is_valid(this.state.phone_valid)} >
						<input className={"input2 " + this.field_has_value(this.state.phone)} type="text" name="phone"
            value={this.state.phone}
            onChange={event => {
              this.setState({ phone: event.target.value });
            }}
            onFocus={event => {
              this.setState({ phone_valid: true})
            }}/>
						<span className="focus-input2" data-placeholder="شماره تلفن"></span>
					</div>

          <div className="container-contact2-form-btn">
            <a href="/signup">اکانت ندارم</a>
          </div>
					<div className="container-contact2-form-btn">
						<div className="wrap-contact2-form-btn">
							<div className="contact2-form-bgbtn"></div>
							<button className="contact2-form-btn" type="submit">
								ورود
							</button>
						</div>
					</div>
				</form>
          </div>
        </div>
      </div>
    );
  }
}

